export default {
	data() {
		return {
			name: '赵鸿飞',
			numPerPage: 20, //展示条数
			Data: []
		}
	},
	created() {
		this.init()
	},
	mounted() {
		uni.showLoading({
			title: '加载中'
		});
	},
	methods: {
		init() {
			uni.request({
				url: this.$store.state.url + '/SO/GetSOs',
				method: 'GET',
				data: {
					pageNum: '1',
					numPerPage: this.numPerPage,
					orderField: '',
					orderDirection: '',
					search: ''
				},
				header: {
					'Token': this.$store.state.token
				},
				success: (res) => {
					this.Data = JSON.parse(res.data).Data
					console.log(this.Data)
					uni.showLoading({
						title: '加载中'
					});
					setTimeout(function() {uni.hideLoading();}, 100);
				}
			});
		},
		order(item) {
			this.$store.state.SOId = item.SOId
			uni.navigateTo({
				url: "./Order/Order",
			});

		}
	}
}
